.top-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  padding: 0.4rem;
  background-color: #2f6073;
  border-bottom-right-radius: 15px;
  -webkit-box-shadow: 2px 2px 2px black;
          box-shadow: 2px 2px 2px black;
  opacity: 0.85;
  -webkit-transition: 200ms;
  transition: 200ms;
}
.top-wrapper.on-call {
  background-color: transparent !important;
  -webkit-box-shadow: none;
          box-shadow: none;
}
.top-wrapper:hover {
  opacity: 1;
}
.top-wrapper .id-text {
  color: white;
  font-size: 150%;
}
.top-wrapper .id-text span {
  color: rgb(20, 120, 138);
}
.call-wrapper {
  text-align: center;
  margin-top: 20vh;
}
.call-wrapper .header {
  font-size: 98px;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  letter-spacing: 7px;
}
.call-wrapper .header .one {
  display: inline-block;
  -webkit-transform: translateX(25px) rotateY(45deg) rotateX(25deg);
          transform: translateX(25px) rotateY(45deg) rotateX(25deg);
}
.call-wrapper .header .two {
  display: inline-block;
  -webkit-transform: translateX(-25px) rotateY(-45deg) rotateX(35deg);
          transform: translateX(-25px) rotateY(-45deg) rotateX(35deg);
}
.call-wrapper .banner {
  margin-bottom: 1.5rem;
  margin-top: 0.5rem;
  letter-spacing: 10px;
  opacity: 0.7;
}
.call-wrapper .form-wrapper * {
  -webkit-box-shadow: 0px 5px 1px 1px rgb(30, 28, 31);
          box-shadow: 0px 5px 1px 1px rgb(30, 28, 31);
  padding: 0.6rem 0.8rem;
  font-size: 110%;
  outline: none;
  border: none;
}
.call-wrapper .form-wrapper input {
  color: #011526;
  background-color: #f2f2f2;
}
.video-wrapper {
  position: relative;
  width: 100%;
  height: 100vh;
  display: grid;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: black;
  overflow: hidden;
}
.video-wrapper .remote-video {
  text-align: center;
}
.video-wrapper .self-video {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 300px;
  -webkit-box-shadow: -5px -5px 5px -4px black;
          box-shadow: -5px -5px 5px -4px black;
}
.video-wrapper .call-menu-bar {
  position: absolute;
  bottom: 30px;
  width: 100%;
  text-align: center;
}
.video-wrapper .call-menu-bar button {
  border: none;
  outline: none;
  opacity: 0.9;
  background-color: rgb(82, 19, 19);
  color: #f2f2f2;
  border-radius: 50%;
  font-size: 150%;
  padding: 1.2rem;
  -webkit-transition: 300ms;
  transition: 300ms;
  cursor: pointer;
  -webkit-transform: rotate(225deg);
          transform: rotate(225deg);
}
.video-wrapper .call-menu-bar button:hover {
  opacity: 1;
}
@media only screen and (max-width: 480px) {
.video-wrapper .self-video {
    width: 200px;
    top: 0;
    -webkit-box-shadow: none;
            box-shadow: none;
}
}

.calling-banner {
  position: fixed;
  z-index: 3;
  top: 0;
  display: grid;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: end;
  text-align: center;
  width: 100%;
  height: 100vh;
  background-color: rgba(221, 192, 192, 0.932);
  color: rgb(51, 39, 39);
}
.calling-banner .image-wrapper i {
  display: inline-block;
  margin: 0 auto;
  width: 62px;
  font-size: 180px;
  -webkit-animation: calling-banner-anim 1000ms infinite alternate;
          animation: calling-banner-anim 1000ms infinite alternate;
  -webkit-animation-delay: 1000ms;
          animation-delay: 1000ms;
}
.calling-banner .calling-text {
  font-size: 75px;
  margin-bottom: 15vh;
}
@-webkit-keyframes calling-banner-anim {
0% {
    -webkit-transform: translateX(-25px) rotate(-15deg);
            transform: translateX(-25px) rotate(-15deg);
}
20% {
    -webkit-transform: rotate(15deg) translateX(-35px);
            transform: rotate(15deg) translateX(-35px);
}
30% {
    -webkit-transform: translateX(-25px) rotate(-15deg);
            transform: translateX(-25px) rotate(-15deg);
}
70% {
    -webkit-transform: rotate(15deg) translateX(-35px);
            transform: rotate(15deg) translateX(-35px);
}
}
@keyframes calling-banner-anim {
0% {
    -webkit-transform: translateX(-25px) rotate(-15deg);
            transform: translateX(-25px) rotate(-15deg);
}
20% {
    -webkit-transform: rotate(15deg) translateX(-35px);
            transform: rotate(15deg) translateX(-35px);
}
30% {
    -webkit-transform: translateX(-25px) rotate(-15deg);
            transform: translateX(-25px) rotate(-15deg);
}
70% {
    -webkit-transform: rotate(15deg) translateX(-35px);
            transform: rotate(15deg) translateX(-35px);
}
}

.call-accept-modal-wrapper {
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(51, 58, 58, 0.808);
  display: grid;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.call-accept-modal-wrapper .modal-container {
  display: inline-block;
  width: 480px;
  min-height: 30vh;
  margin: 0 auto;
  text-align: center;
  color: rgb(219, 200, 200);
  background: -webkit-gradient(linear, left top, left bottom, from(rgb(122, 90, 90)), to(rgb(11, 11, 37)));
  background: linear-gradient(rgb(122, 90, 90), rgb(11, 11, 37));
  padding: 0.5rem;
  -webkit-box-shadow: 5px 5px 15px -2px black;
          box-shadow: 5px 5px 15px -2px black;
  position: relative;
}
.call-accept-modal-wrapper .modal-container .text-wrapper {
  font-size: 35px;
  margin-bottom: 7vh;
  margin-top: 5vh;

  font-weight: bolder;
}
.call-accept-modal-wrapper .modal-container .button-wrapper {
  display: grid;
  grid-template-columns: 50% auto;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
.call-accept-modal-wrapper .modal-container button {
  padding: 0.3rem 0;
  color: white;
  border: none;
  font-size: 150%;
  opacity: 0.9;
  padding: 2rem;
  outline: none;
}
.call-accept-modal-wrapper .modal-container button:hover {
  opacity: 1;
}
.call-accept-modal-wrapper .modal-container button.accept {
  background-color: rgb(20, 56, 20);
}
.call-accept-modal-wrapper .modal-container button.decline {
  background-color: rgb(87, 24, 24);
}
.modal-enter-active,
.modal-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.modal-enter,
.modal-leave-to {
  opacity: 0;
}
@media screen and (max-width: 480px) {
.call-accept-modal-wrapper .modal-container {
    width: 100%;
    height: 100%;
    padding-top: 30vh;
}
.call-accept-modal-wrapper .modal-container button {
    padding: 3rem 0;
}
}
